.componentePlan{
    width: 80%;
    margin-left: 5%;
    justify-content: flex-end;
}
.conjuntoDeTarjetas{
    display: flex;
    justify-content: space-evenly;
}
.tarjetaPlan {
    width: 100%;
    margin-left: 2%;
    height: 40%;
    position: relative;
    border: 8px solid transparent;
    border-radius: 16px;
    background: #FBFBFBFB;
    background-clip: padding-box;
    padding: 10px;
}
.tarjetaPlan::after{
    position: absolute;
    top: -8px; bottom: -8px;
    left: -8px; right: -8px;
    background: linear-gradient(rgba(21,27,102,1), rgba(0,224,209,1));
    content: '';
    z-index: -1;
    border-radius: 16px;
}
.numeroCantidad{
    font-family: "GothamMedium";
    line-height: 1;
    margin: 0;
    font-size: 60px;
    background: -webkit-linear-gradient(rgba(21,27,102,1), rgba(0,224,209,1));
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
}
.descripciondeLosNumeros{
    font-size: 12px;
}
.TituloPlanes{
    font-family: "GothamMedium";
    margin: 0;
    margin-bottom: 6%;
}
.botonMas{
    width: 70%;
    margin-left: 50%;
    display: flex;
    justify-content: flex-end;
    cursor: pointer;
    line-height: 0;
}
.botonMas img{
    margin: 2%;
}
.numeroLlamadasMensajes{
    display: flex;
    line-height: 0;
}
.numeroLlamadasMensajes img{
    margin: 2%;
    margin-right: 10%;
}
@media screen and (min-width:768px) and (max-width:1024px){
   .numeroCantidad{
       font-size: 45px;
   }
   .botonMas{
       line-height: 1;
       margin-left: 40%;
       width: 80%;
   }
}
@media screen and (min-width:424px) and (max-width:767px){
    .numeroCantidad{
        font-size: 30px;
    }
    .botonMas{
        line-height: 1;
        margin-left: 40%;
        width: 80%;
    }
    .conjuntoDeTarjetas{
        flex-direction: column;
    }
    .TituloPlanes{
        font-size: 15px;
    }
 }