.componenteGrupal{
    width: 40%;
    border-radius: 20px;
    font-family: "GothamMedium";
    margin-left: 2%;
}
.textoAnesdeBarra{
    display: flex;
    line-height: 0.5;
    align-items: center;
    justify-content: space-evenly;
    font-size: 14px;
}
.tituloMetaGrupal{
    display: flex;
    line-height: 0.5;
    margin-left: 5%;
}
.barra{
    margin-left: 5%;
    width: 90%;
    position: relative;
    background-color: #FBFBFB;
    border: 0px solid transparent;
    border-radius: 3px;
}
.barra div{
    position: relative;
    width: 0;
    height: 15px;
    border-radius: 3px;
    background-color: #FF671B;
}
.skills:not(:last-child){
    margin-bottom: 30px;
}
#html-barra{
    animation: html-fill 2s forwards;
}
@keyframes html-fill{
    100%{
        width: 50%;
    }
}
@media screen and (min-width:768px) and (max-width:1024px){
    .textoAnesdeBarra{
        font-size: 13px;
    }
    .tituloMetaGrupal{
        font-size: 16px;
    }
}
@media screen and (min-width:425px) and (max-width:768px){
    .textoAnesdeBarra{
        font-size: 13px;
    }
    .tituloMetaGrupal{
        font-size: 16px;
    }
}
@media screen and (min-width:100px) and (max-width:425px){
    .textoAnesdeBarra{
        font-size: 13px;
    }
    .tituloMetaGrupal{
        font-size: 16px;
    }
    .componenteGrupal{
        width: 100%;
        margin-top: 10%;
        padding: 8%;
    }
}
@media screen and (min-width:100px) and (max-width:768px){
    .textoAnesdeBarra{
        font-size: 13px;
    }
    .tituloMetaGrupal{
        font-size: 16px;
    }
    .componenteGrupal{
        width: 50%;
        margin-top: 10%;
        padding: 3%;
    }
}