.componenteContactos{
    display: flex;
    position: fixed;
    z-index: 3;
    height: 100vh;
    width: 15%;
    right: 0;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    justify-content: space-evenly;
}
.notificaciones{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.tarjeta1{
    margin-top: 10%;
    background-color: #FBFBFB;
    border-radius: 20px;
    width: 150px;
}
.arribaTarjeta{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-evenly;
    line-height: 0;
}
.iconoRedSocial{
    width: 15%;
}
.abajoTarjeta{
    margin-top: 2%;
    background-color: #E9E9E9;
    padding: 1%;
    line-height: 1;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}
.flechaContacto{
    width: 5%;
    cursor: pointer;
}
.nombrePersona{
    line-height: 0;
    font-size: 15px;
}
@media screen and (min-width:425px) and (max-width:1024px){
    .nombrePersona{
        font-size: 14px;
    }
    .arribaTarjeta{
        font-size: 13px;
    }
    .tarjeta1{
        width: 110%;
        padding: 3%;
    }
    .abajoTarjeta p{
        font-size: 12px;
    }
}
@media screen and (min-width:100px) and (max-width:425px){
    .componenteContactos{
        display: none;
    }
}