.componenteIndividualMeta{
    width: 70%;
    border-radius: 20px;
    font-family: "GothamMedium";
    padding: 1%;
}
.ubicacionContenedor{
    display: flex;
    padding: 2%;
    align-items: center;
    align-content: center;
    justify-content: space-around;
}
.objetivosIndividual{
    width: 95%;
    justify-content: space-between;
    display: flex;
    font-size: 14px;
}
.medidasIndividual{
    display: flex;
    align-items: center;
}
.primeraLineaMeta{
    width: 100%;
    margin: 3%;
    flex-direction: column;
}
.circular{
    color: #000010;
    font-size: 25px;
    width: 100px;
    height: 100px;
    position: relative;
    transform: scale(1.5);
}
.circular .inner{
    position: absolute;
    z-index: 6;
    top: 50%;
    left: 50%;
    height: 80px;
    width: 80px;
    margin: -40px 0 0 -40px;
    background-color: #FBFBFB;
    border-radius: 100%;
    box-shadow: 0 1px 0 rgba(0,0,0,0.2);
}
.circular .numb{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
}
.circular .bar{
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #E9E9E9;
    -webkit-border-radius: 100%;
    clip: rect(0px, 100px, 100px, 49.9px);
}
.circle .bar .progress{
    position: absolute;
    height: 100%;
    width: 100%;
    -webkit-border-radius: 100%;
    clip: rect(0px, 50px, 100px, 0px);
    background-color: #FF671B;
}
.circle .left .progress{
    z-index: 1;
    animation: left 3s linear both;
}
@keyframes left{
    100%{
        transform: rotate(180deg);
    }
}
.circle .right{
    z-index: 3;
    transform: rotate(180deg);
}
.circle .right .progress{
    animation: right 4s linear both;
    animation-delay: 4s;
}
@keyframes right{
    100%{
        transform: rotate(0deg);
    }
}
@media screen and (min-width:768px) and (max-width:1024px){
    .objetivosIndividual{
        font-size: 13px;
    }
    .medidasIndividual{
        flex-direction: column;
    }
}
@media screen and (min-width:425px) and (max-width:768px){
    .objetivosIndividual{
        font-size: 13px;
    }
    .medidasIndividual{
        flex-direction: column;
    }
    .ubicacionContenedor{
        flex-direction: column;
    }
    .circular{
        margin: 3%;
        transform: scale(1.1);
    }
    .componenteIndividualMeta{
        width: 60%;
    }
    .objetivosIndividual{
        width: 100%;
    }
    .descripcionObjetivoIndividual{
        margin-right: 1%;
    }
}
@media screen and (min-width:100px) and (max-width:425px){
    .objetivosIndividual{
        font-size: 13px;
    }
    .medidasIndividual{
        flex-direction: column;
    }
    .ubicacionContenedor{
        flex-direction: column;
    }
    .circular{
        margin: 3%;
        transform: scale(1.1);
    }
    .componenteIndividualMeta{
        width: 100%;
    }
    .objetivosIndividual{
        width: 100%;
    }
    .descripcionObjetivoIndividual{
        margin-right: 1%;
    }
}