.componenteFaltante{
    width: 100%;
    font-family: "GothamMedium";
}
.tablaGeneral{
    width: 100%;
    color: #000010;
    border: 1px solid transparent;
    border-collapse: collapse;
}
.filasTabla{
    border-right: 1px solid white;
    font-size: 14px;
    background-color: #FBFBFB;
    height: 50px;
}
.casillaFinal{
    font-size: 15px;
    height: 15px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}
.EncabezadYFinalTabla{
    padding: 1%;
    padding-left: 0;
    padding-right: 0;
    border-radius: 20px;
}
.casilla1{
    width: 40%;
}
.color5{
    border-left: 6px solid #FFCF11;
}
.color3{
    border-left: 6px solid #6F52DA;
}
.color2{
    border-left: 6px solid #81CC47;
}
.color1, .color4{
    border-left: 6px solid #00B2A6;
}
.fondoGris{
    background-color: #E9E9E9;
}
.opcionesContactoTabla{
    user-select: none;
    display: flex;
    margin-top: 10%;
    justify-content: space-evenly;
    align-content: center;
}
.circulo{
    width: 20px;
    height: 20px;
    border: 1px solid #3b3b3b;
    color: #E9E9E9;
    background-color: #a1a1a1;
    border-radius: 50%;
}
.opcionesContactoTabla img, .circulo{
    cursor: pointer;
}
.colorAmarillo{
    color: #FFCF11;
    border: 1px solid transparent;
    background-color: #FFCF11;
}
.descuentoTabla{
    color: #636363;
    font-size: 12px;
}
.casillaLlamar{
    width: 30%;
    line-height: 0;
    justify-content: flex-start;
}
.TituloTabla{
    display: flex;
    margin-left: 10%;
    justify-content: flex-start;
}
@media screen and (min-width:768px) and (max-width:1024px){
    .casillaLlamar{
        font-size: 13px;
    }
    .descuentoTabla{
        line-height: 1;
        font-size: 11px;
    }
    .circulo{
        width: 15px;
        height: 15px;
    }
    .TituloTabla{
        font-size: 14px;
    }
}
@media screen and (min-width:425px) and (max-width:768px){
    .componenteFaltante{
        margin-top: 8%;
    }
}
@media screen and (min-width:100px) and (max-width:425px){
    .TituloTabla{
        font-size: 12px;
    }
    .opcionesContactoTabla{
        flex-direction: column;
        align-items: center;
    }
    .opcionesContactoTabla img{
        width: 30%;
        margin-top: 5%;
    }
    .descuentoTabla{
        line-height: 1;
        margin-top: 1%;
    }
    .componenteFaltante{
        margin-top: 10%;
    }
}