.componenteMenu{
    display: flex;
    flex-direction: column;
    width: 8%;
    height: 100vh;
    position: fixed;
    padding-right: 3%;
}
/*Logo Kuepa*/
.logoKuepa{
    background-color: #FBFBFB;
    justify-content: flex-start;
}
.logoKuepa img{
    width: 60%;
    cursor: pointer;
}
/*Organizar*/
.paginasDelMenu, .usuarioNotifi{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.parteAbajoMenu{
    display: flex;
    flex-direction: column;
    margin-top: 90%;
    justify-content: flex-end;
}
/*Imagenes los iconos de paginas*/
.paginasDelMenu img{
    width: 30%;
    margin-top: 30%;
    cursor: pointer;
}
/*Parte usuario*/
.usuarioNotifi{
    margin-top: 100%;
}
.notifi{
    width: 30%;
    cursor: pointer;
}
.ana{
    margin-top: 40%;
    width: 50%;
    cursor: pointer;
}
@media screen and (min-width:100px) and (max-width:425px){
    .componenteMenu{
        display: none;
        flex-direction: row;
        width: 100%;
        height: 10%;
    }
    .componenteMenu img{
        width: 1%;
    }
    .paginasDelMenu img{
        flex-direction: row;
    }
    .parteAbajoMenu, .parteAbajoMenu img{
        flex-direction: row;
        margin: 0;
    }
}